<template>
    <div class="mechanism">
        <div class="mechTop">
            <div class="topTitle">
                <p>新闻动态</p>
            </div>
        </div>
        <div class="mechContent">
            <el-container>
                <el-aside width="250px">
                    <div class="treeTitle"><span>全部</span></div>
                    <div class="treeData">
                        <el-tree
                            :data="data"
                            :props="defaultProps"
                            accordion
                            @node-click="handleNodeClick">
                        </el-tree>
                    </div>
                </el-aside>
                <el-container>
                    <el-main class="serviceBox">
                        <div class="newsLeft" v-for="(item,index) in newsArr" :key="index">
                            <div class="newsImg"></div>
                            <div class="newsCoent">
                                <div class="coentLeft"><h1>{{item.year}}</h1><p>{{item.date}}</p></div>
                                <div class="bord"></div>
                                <div class="coentRight">
                                    <p>{{item.title}}</p>
                                    <span>{{item.remarks}}</span>
                                </div>
                            </div>
                        </div>
                    </el-main>
                </el-container>
            </el-container>
        </div>
    </div>
</template>
<script>
import {getUserNotify} from '@/request'
export default {
    data() {
        return{
            data: [{
                label: '平台动态',
                children: [] 
            },{
                label: '行业新闻',
                children: []
            }],
            defaultProps: {
                children: 'children',
                label: 'label'
            },
            newsArr: [
                // {
                //     title: '依法执业防范纠纷 构建和谐医患关系',
                //     year: '2019',
                //     date: '12-13',
                //     coent: '依法执业防范纠纷 构建和谐医患关系'
                // },
                // {
                //     title: '依法执业防范纠纷 构建和谐医患关系',
                //     year: '2019',
                //     date: '12-13',
                //     coent: '依法执业防范纠纷 构建和谐医患关系'
                // },
                // {
                //     title: '依法执业防范纠纷 构建和谐医患关系',
                //     year: '2019',
                //     date: '12-13',
                //     coent: '依法执业防范纠纷 构建和谐医患关系'
                // }
            ]
        }
    },
    methods: {
         handleNodeClick(data) {
            console.log(data);
        },
        dateFormat(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 拼接
            return year+"-"+month+"-"+day;
        },
        getNotify() {
            getUserNotify({page:1,size: 10}).then((res) => {
                this.newsArr = res.data.list
                this.newsArr.map((item) => {
                    item.updateDate = this.dateFormat(item.updateDate)
                    item.year = item.updateDate.slice(0,4)
                    item.date = item.updateDate.slice(5,item.updateDate.length)
                })
            })
        }
    },
    mounted() {
        this.getNotify()
    },
}
</script>>
<style scoped>
.mechTop{
    width: 100%;
    height: 315px;
    background-image: url(../assets/newsInformation.png);
    background-repeat:no-repeat;
    background-size: cover;
    position: relative;
}
.topTitle{
    height: 90px;
    background-color: #fff;
    margin: 0 10%;
    position: absolute;
    bottom: 0;
    left: 10%;
    right: 10%;
    border: 1px solid rgb(227, 227, 227);
}
.topTitle p{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 20px;
    color: rgb(16, 16, 16);
    margin: 0 35px;
    line-height: 90px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
}
.mechContent{
    padding: 30px 10%;
}
.treeTitle{
    border-left: 2px solid rgb(13, 114, 183);
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 14px;
    color: rgb(16, 16, 16);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 20px;
    padding-left: 20px;
    text-align: left
}
.newsLeft{
    width: 47%;
    float: left;
    margin-right: 3%;
    margin-bottom:30px
}
.newsLeft .newsImg {
    width: 100%;
    height: 180px;
    background-image: url(../assets/news.png);
    background-repeat:no-repeat;
    background-size: cover;
}
.newsLeft .newsCoent{
    margin-top: 15px;
    height: 75px;
    border-bottom: 1px solid #ccc;
}
.newsLeft .coentRight{
    border: none;
    text-align: left;
    float: left;
    width: 70%
}
.newsLeft .coentRight p{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 16px;
    color: rgb(16, 16, 16);
    margin: 6px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis
}
.newsLeft .coentRight span {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
.coentLeft{
    width: 20%;
    float: left;
}
.coentLeft h1{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 28px;
    color: rgb(16, 16, 16);
    margin: 0;
    text-align: left;
}
.coentLeft p{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 18px;
    color: rgba(174, 174, 174, 1);
    margin: 0;
    text-align: left
}
.coentRight{
    height: 50px;
    border-left: 1px solid #ccc;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 14px;
    color: rgba(174, 174, 174, 1);
}
.bord{
    width: 1px;
    height: 40px;
    background: #ccc;
    float: left;
    margin-top: 13px;
    margin-right: 10px
}
</style>
<style>
.el-tree{
    margin-top: 15px;
    border-top: 1px solid #ccc
}
.el-tree-node__content{
    height: 45px !important;
    border-bottom: 1px solid #ccc
}
.el-tree-node:focus>.el-tree-node__content{
    background-color: rgb(13, 114, 183) !important;
    color: #fff
}
.el-tree-node__content:hover{
    background-color: rgb(13, 114, 183) !important;
    color: #fff
}
</style>


